import React, { useState } from 'react';
import { useDispatch } from 'react-redux';
import styled from 'styled-components';
import { fetchPosts } from '../Feed/redux/actions';
import { axiosInstance } from '../../config/axios';


const Form = () => {
  const dispatch = useDispatch();
  const [text, setText] = useState('');

  const handleSubmit = (e) => {
    e.preventDefault();
    const data = { content: text };
    axiosInstance.post('posts/', data).then(() => {
      dispatch(fetchPosts());
    });
  };
  return (
    <StForm onSubmit={handleSubmit}>
      <input type="text" onChange={e => setText(e.target.value)} placeholder="Share something with the team!" />
      <button type="submit">Post</button>
    </StForm>
  );
};

Form.propTypes = {};

export default Form;

const StForm = styled.form`
  border-radius: 8px;
  background: white;
  margin-bottom: 20px;
  height: 60px;
  display: flex;
  padding: 16px;
  align-items: center;
  
  input {
    height: 32px;
    flex: 1;
    margin-right: 20px;
    border-radius: 14px;
    background: #f3f3f3;
    border: none;
    font-family: 'Darwin pro';
    padding-left: 10px;
  }

  button {
    background: none;
    border-radius: 8px;
    padding: 6px 18px;
    border: 2px solid #ffced9;
    font-family: 'Darwin Pro';
    color: #ff507d;
  }
`;
