/* eslint-disable jsx-a11y/accessible-emoji */
import React, { useEffect, useState } from 'react';
import PropTypes from 'prop-types';
import { useParams } from 'react-router-dom';
import styled from 'styled-components/macro';
import { axiosInstance } from '../../config/axios';
import Post from '../../components/Post';


const Profile = (props) => {
  const { id } = useParams();
  const [profile, setProfile] = useState();

  useEffect(() => {
    axiosInstance.get(`users/${id}/`).then((result) => {
      setProfile(result.data);
    }).catch((error) => {
      console.error(error);
    });
  }, []);

  if (!profile) return null;

  return (
    <StProfile>
      <UserHeader>
        <AvatarContainer>
          <Avatar src={profile.photo} alt="" />
        </AvatarContainer>
        <div>
          <h1>{`${profile.first_name} ${profile.last_name}`}</h1>
          <span>
            <Label> Working at 🐙since: </Label>
            09 July 2014
          </span>
        </div>
        <Right>
          <span>
            <Label>🎂 </Label>
            {profile.day_of_birth}
          </span>
          <span>
            <Label>📍 </Label>
            Montevideo
          </span>
        </Right>
      </UserHeader>
      <PostContainer>
        <h2>Posts</h2>
        {profile.posts.map(p => <Post info={p} />)}
      </PostContainer>
    </StProfile>
  );
};

Profile.propTypes = {
};
export default Profile;

const StProfile = styled.div`
  display: flex;
  flex-direction: column;
  width: 60%;
  margin: 0 auto;
`;

const AvatarContainer = styled.div`
    border-radius: 50%;
    border: 10px solid white;
    position: relative;
    width: 150px;
    height: 150px;
    overflow: hidden;
`;

const Avatar = styled.img`
    position: absolute;
    left: -10000%;
    right: -10000%;
    top: -10000%;
    bottom: -10000%;
    margin: auto auto;
    max-width: 10%;
    max-height: 10%;
    transform: scale(10);
`;

const UserHeader = styled.div`
  display: flex;
  position: relative;
  margin-bottom: 20px;
  align-items: center;

  h1 {
    font-size: 28px;
    margin-top: 0;
    margin-bottom: 8px;
  }

  :before {
    content: '';
    position: absolute;
    height: 70%;
    background: white;
    width: calc(100% - 20px);
    z-index: -1;
    top: 15%;
    border-radius: 8px;
    left: 20px;
    box-shadow: 0 1px 2px lightgrey;
  }
`;

const Label = styled.span`
  color: gray;
`;

const Right = styled.div`
  position: absolute;
  right: 20px;
  display: flex;
  flex-direction: column;
`;

const PostContainer = styled.div`
  width: 600px;
  margin: 0 auto;
`;
