import React from 'react';
import styled from 'styled-components';

function importAll(r) {
  return r.keys().map(r);
}

const [first, second, ...images] = importAll(require.context('../../../fotos', false, /\.(png|jpe?g|svg)$/));

const Gallery = () => (
  <div>
    <StGallery>
      <h1>Octobot's Gallery</h1>
      <Featured>
        <Card featured>
          <Overlay>
            <span>A day at Octobot's office</span>
          </Overlay>
          <Image src={first} alt="d" />
        </Card>
        <Card featured>
          <Overlay>
            <span>A day at Octobot's office</span>
          </Overlay>
          <Image src={second} alt="d" />
        </Card>
      </Featured>
      {images.map(i => (
        <Card>
          <Overlay>
            <span>A day at Octobot's office</span>
          </Overlay>
          <Image src={i} alt="d" />
        </Card>
      ))}
    </StGallery>
  </div>
);

export default Gallery;


const Featured = styled.div`
  width: 100%;
`;

const StGallery = styled.div`
  display: flex;
  flex-wrap: wrap;
  margin: 0 10px;
`;

const Card = styled.div`
  width: ${props => (props.featured ? 'calc(100% - 20px)' : 'calc(50% - 20px)')};
  float: right;
  position: relative;
  margin: 10px;
  overflow: hidden;

  @media(min-width:768px) {
    width: ${props => (props.featured ? 'calc(50% - 20px)' : 'calc(33.3% - 20px)')};
  }
`;

const Image = styled.img`
    width: 100%;
    display: block;
    transition: 0.2s transform ease-in-out;

    ${Card}:hover & {
      transform: scale(1.1);
    }
`;


const Overlay = styled.div`
    position: absolute;
    width: 100%;
    height: 100%;
    background: rgba(0, 0, 0, 0.2);
    transition: 0.2s opacity ease-in-out;
    opacity: 1;
    z-index: 1;

    @media(min-width:768px) {
      opacity: 0;
      background: rgba(0, 0, 0, 0.5);
    }

    span {
      position: absolute;
      bottom: 20px;
      color: white;
      font-size: 20px;
      font-weight: 100;
      left: 20px;
    }

    ${Card}:hover & {
      opacity: 1;
    }
`;
