import { LOGIN, LOGOUT } from './types';

const API_CALL = 'API_CALL';

export const loginAction = (username, password) => ({
  type: API_CALL,
  payload: {
    ...LOGIN,
    method: 'post',
    url: '/auth/',
    data: {
      username,
      password
    }
  }
});

export const logoutAction = () => ({
  type: API_CALL,
  payload: {
    ...LOGOUT,
    method: 'post',
    url: '/logout/'
  }
});
