import React, { useState, useEffect } from 'react';
import { useDispatch, useSelector } from 'react-redux';
import { useHistory, Redirect } from 'react-router-dom';
import Form from './Form';
import { loginAction } from './redux/actions';
import Pepocho from '../../resources/pepocho.png';
import StLogin from './StLogin';
import Error from './Error';
import Auth from '../../utils/Auth';
import Button from '../../components/Button';

const Login = () => {
  const [user, setUser] = useState('');
  const [password, setPassword] = useState('');
  const loginStatus = useSelector(state => state.auth.status);

  const dispatch = useDispatch();
  const history = useHistory();


  const [errors, setErrors] = useState({});

  useEffect(() => {
    setErrors({});
  }, [user, password]);

  useEffect(() => {
    if (loginStatus.success) {
      history.push('/');
    }
  }, [loginStatus]);

  const handleSubmit = (e) => {
    e.preventDefault();
    const newErrors = {};
    if (user === '') newErrors.user = 'Usuario no puede ser vacío';
    if (password === '') newErrors.password = 'Contraseña no puede ser vacía';
    setErrors(newErrors);

    if (Object.keys(newErrors).length === 0) {
      dispatch(loginAction(user, password));
    }
  };

  if (Auth.getInstance().isAuthenticated()) {
    return <Redirect to="/" />;
  }
  return (
    <StLogin>
      <img src={Pepocho} alt="" width="500" />
      <Form onSubmit={handleSubmit}>
        <h1>Octobot&#39;s Community</h1>
        <label>
          Usuario
          <input type="text" onChange={e => setUser(e.target.value)} />
          {errors.user && <Error>{errors.user}</Error>}
        </label>
        <label>
          Contraseña
          <input type="password" onChange={e => setPassword(e.target.value)} />
          {errors.password && <Error>{errors.password}</Error>}
        </label>
        <Button type="submit" action="primary" text="Ingresar" />
      </Form>
    </StLogin>
  );
};

export default Login;
