import styled, { css } from 'styled-components/macro';

const StSidebar = styled.div`
  width: 280px;
  height: 100%;
  display: ${props => (props.placement === 'right' ? 'none' : '')};
  
  >div {
    padding: 0 32px 32px;
    overflow-y: scroll;
    position: fixed;
    height: calc(100% - 80px);
    width: 220px;
  }

  ${props => props.placement === 'right' && css`
    @media (min-width: 1024px) {
      display: block;
    }`
}
`;

export default StSidebar;
