import React from 'react';
import styled from 'styled-components';

const StButton = styled.button`
  background-color: ${props => (props.action === 'primary' ? '#ff3d61' : 'lightblue')} ;
  border-radius: 8px;
  height: 48px;
  border: none;
  color: white;
  font-size: 18px;
`;

const Button = ({ type, text, action }) => (
  <StButton type={type} action={action}>
    { text }
  </StButton>
);

export default Button;
