/* eslint-disable no-param-reassign */
import {
  FETCH
} from './types';

const INITIAL_STATE = {
  posts: []
  /* initialize state */
};

export default function (state = INITIAL_STATE, action) {
  switch (action.type) {
    /* ******************************** */
    /*            FETCH LIST            */
    /* ******************************** */
    case FETCH.success:
      return {
        ...state,
        posts: action.payload
      };
    default:
      return state;
  }
}
